import 'svelte/internal/disclose-version';
import * as $ from 'svelte/internal/client';
import { onMount } from "svelte";
import { log } from "../src-helpers/logger";
import { GetApiFolders as getClubFolders } from "../generated/paths-public";
import FolderTree from "./FolderTree.svelte"; // Import the tree component

var root_1 = $.template(`<p>Loading folders...</p>`);
var root_3 = $.template(`<p class="error svelte-s0d47nld8ldc"> </p>`);
var root_5 = $.template(`<p>No folders available.</p>`);
var root_7 = $.template(`<p> </p>`);
var root = $.template(`<div class="club-folder-selector svelte-s0d47nld8ldc"><!> <!></div>`);

function ClubFolderSelection($$anchor, $$props) {
	$.push($$props, true);

	// Props
	const initialFolderId = $.prop($$props, 'initialFolderId', 3, null);
	// Reactive state
	let folders = $.state($.proxy([]));
	let selectedFolderId = $.state($.proxy(initialFolderId()));
	let isLoading = $.state(true);
	let error = $.state(null);

	// Fetch folders on mount
	onMount(async () => {
		$.set(isLoading, true);
		$.set(error, null);

		try {
			const result = await getClubFolders($$props.clubId);

			if (result.ok) {
				// Assuming result.data is the array of Folder objects
				$.set(folders, result.data, true);
			} else {
				log.error(`Failed to fetch club folders: ${result.statusText}`);
				$.set(error, `Failed to load folders: ${result.statusText}`);
			}
		} catch(err) {
			log.error("Error fetching club folders:", err);
			$.set(error, "An error occurred while loading folders.");
		} finally {
			$.set(isLoading, false);
		}
	});

	// Handle selection event from FolderTree
	function handleFolderSelect(event) {
		$.set(selectedFolderId, event.detail, true);
		console.log("Selected folder ID:", $.get(selectedFolderId)); // Potentially dispatch another event or perform an action here
	}

	function getSelectedFolderId() {
		return $.get(selectedFolderId);
	}

	var div = root();
	var node = $.child(div);

	{
		var consequent = ($$anchor) => {
			var p = root_1();

			$.append($$anchor, p);
		};

		var alternate = ($$anchor, $$elseif) => {
			{
				var consequent_1 = ($$anchor) => {
					var p_1 = root_3();
					var text = $.child(p_1, true);

					$.reset(p_1);
					$.template_effect(() => $.set_text(text, $.get(error)));
					$.append($$anchor, p_1);
				};

				var alternate_1 = ($$anchor, $$elseif) => {
					{
						var consequent_2 = ($$anchor) => {
							var p_2 = root_5();

							$.append($$anchor, p_2);
						};

						var alternate_2 = ($$anchor) => {
							var fragment = $.comment();
							var node_1 = $.first_child(fragment);

							FolderTree(node_1, {
								get folders() {
									return $.get(folders);
								},
								get selectedFolderId() {
									return $.get(selectedFolderId);
								},
								$$events: { select: handleFolderSelect }
							});

							$.append($$anchor, fragment);
						};

						$.if(
							$$anchor,
							($$render) => {
								if ($.get(folders).length === 0) $$render(consequent_2); else $$render(alternate_2, false);
							},
							$$elseif
						);
					}
				};

				$.if(
					$$anchor,
					($$render) => {
						if ($.get(error)) $$render(consequent_1); else $$render(alternate_1, false);
					},
					$$elseif
				);
			}
		};

		$.if(node, ($$render) => {
			if ($.get(isLoading)) $$render(consequent); else $$render(alternate, false);
		});
	}

	var node_2 = $.sibling(node, 2);

	{
		var consequent_3 = ($$anchor) => {
			var p_3 = root_7();
			var text_1 = $.child(p_3);

			$.reset(p_3);
			$.template_effect(() => $.set_text(text_1, `Selected: Folder ${$.get(selectedFolderId) ?? ''}`));
			$.append($$anchor, p_3);
		};

		$.if(node_2, ($$render) => {
			if ($.get(selectedFolderId) !== null) $$render(consequent_3);
		});
	}

	$.reset(div);
	$.append($$anchor, div);
	return $.pop({ getSelectedFolderId });
}

if (import.meta.hot) {
	ClubFolderSelection = $.hmr(ClubFolderSelection, () => ClubFolderSelection[$.HMR].source);

	import.meta.hot.accept((module) => {
		$.cleanup_styles('svelte-s0d47nld8ldc');
		module.default[$.HMR].source = ClubFolderSelection[$.HMR].source;
		$.set(ClubFolderSelection[$.HMR].source, module.default[$.HMR].original);
	});
}

export default ClubFolderSelection;

if (customElements.get('my-element') == null) customElements.define('my-element', $.create_custom_element(ClubFolderSelection, { clubId: {}, initialFolderId: {} }, [], ['getSelectedFolderId'], true));
import "bun-svelte:ClubFolderSelection.svelte-f75tf9tjl7mc-style.css";